@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lora:wght@600&display=swap);
body {
  font-family: 'Lora', sans-serif;
  font-weight: 300;
}

h1, h2, h3 {
  font-weight: 300;
  margin: 30px 0
}

a, a:hover {
  color: inherit;
  text-decoration: none;
}


img {
  width: 100%;
}
.artist-card {
    margin-bottom: 40px;
}

.artist-card img {
    height: 180px;
    object-fit: cover;
}

.search {
    margin-bottom: 10px;
}


.spinner {
    width: 20px;
    height: 20px;
    border-left: 3px solid black;
    border-right: 3px solid black;
    border-bottom: 3px solid rgb(201, 201, 201);
    border-left: 3px solid rgb(201, 201, 201);
    border-radius: 50%;
    -webkit-animation: rotate-me 1s linear infinite;
            animation: rotate-me 1s linear infinite
}

@-webkit-keyframes rotate-me {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}

@keyframes rotate-me {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}
.hello {
    margin-left: 40px;
}

.linkBack {
  position: fixed;
  bottom: 200px;
  left: 60px;
}

.imageProfile {
  height: 200px;
  width: 170px;
  padding-bottom: 40px;
}
.header {
    margin-left: 200px;
    margin-top: 200px;
}


